/* Mint */
body.admin-color-mint .chosen-container-active .chosen-single {
	border-color: #4f6d59;
	box-shadow: 0 0 0 1px #4f6d59;
}
body.admin-color-mint .chosen-container .chosen-drop {
	border-color: #4f6d59;
	box-shadow: 0 1px 0 1px #4f6d59;
}
body.admin-color-mint .chosen-container.chosen-dropup .chosen-drop {
	box-shadow: 0 -1px 0 1px #4f6d59;
}
body.admin-color-mint .chosen-container .chosen-results li.highlighted {
	background-color: #4f6d59;
}
