	border-color: #c7a589;
	box-shadow: 0 0 0 1px #c7a589;
}
body.admin-color-coffee .chosen-container .chosen-drop {
	border-color: #c7a589;
	box-shadow: 0 1px 0 1px #c7a589;
}
